
	import {defineComponent,ref,toRefs,reactive,onMounted,watch,computed} from 'vue';
	import {useStore} from 'vuex'
	import {useRoute,useRouter} from 'vue-router'
	import { ElMessage } from 'element-plus'
	export default defineComponent({
		name: 'Cart',
		components: {},
		setup(props, context) {
			onMounted(()=>{
				console.log(useStore());
			});
			const store = useStore();
			const router = useRouter();
			let checklist:any = ref([]);
			let checkall:any = ref(false);
			let cartLists = store.getters.getCartLists;
			watch(checkall,function(val:boolean){
				if(val){
					checklist.value.splice(0);
					cartLists.forEach(function(val:any,i:never){
						checklist.value.push(i);
					});
				}else{
					checklist.value.splice(0);
				}
			});
			const goback = ()=>{
				router.back();
			}
			function cart(){
				function addCartNum(i:number){
					store.dispatch('cmAddCartNum', {index: i});
				}
				function reduceCartNum(i:number){
					store.dispatch('cmReduceCartNum', {index: i});
				}
				function delCartGood(i:number){
					store.dispatch('cmDelCartGood',{index:i});
					ElMessage({
					    message: '已从购物车移除!',
					    type: 'success',
					});
				}
				function topay(){
					router.push({name:"Order",params:{}});
				}
				let total = computed(()=>{
					let sum = 0;
					checklist.value.forEach(function(val:number){
						sum +=cartLists[val].price*cartLists[val].nums;
					});
					return sum;
				});
				
				return {addCartNum,reduceCartNum,delCartGood,total,checklist,checkall,cartLists,topay};
			}
			return {...cart(),goback};
		}
	});
	
